.MovieSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #7B435B;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Poppins';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    border-radius: 50px;
  }
  
  .MovieSearch::placeholder {
    color: #7B435B;
    font-family: 'Poppins';
    font-weight: 400;
    opacity: 50%;
  }
  
  .MovieSearch:focus {
    outline-color: #410B13;
  }